___CSS_LOADER_EXPORT___.push([module.id, `*{
    
    margin: 0;
    padding: 0;
    font-family: 'Roboto Mono', monospace;
}

body{
    height: 100vh;
    display: flex;
}
.left{
    margin: 0.25rem;
    padding: 0.5rem;
}
.projects{
    display: flex;
    flex-direction: column;
    gap: 5px;
    color: #e4dced;
}
.project-div-btn{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem;
}
.project-div-btn h3{
    font-size: 20px;
}
.project-div-btn,
.project-form-btn,
.project-name-submit{
    border: none;
    background-color: black;
    color: white;
    font-size: 20px;
    height: 50px;
    width: 300px;
    font-weight: 700;
    border-radius: 8px;
}
#project-name{
    height: 35px;
    border: none;
}
#project-name:focus{    
    border: 5px solid #e4dced; 
    background-color: rgba(185, 243, 243, 0.648);
}

.project-form-btn{
    margin-top: 10px;
    text-align: center;
    justify-content: center;
}
form.project-form{
    display: flex;
    flex-direction: column;
    gap: 5px;
    margin-top: 10px;
}
.right{
    flex: 1;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.note-form-btn{
    align-self: flex-end;
    font-size: 16px;
    width: 100px;
    border-radius: 16px;
}
.note{
    display: flex;
    height: 100px;
    border: 2px rgba(69, 68, 68, 0.849);
    border-style: dashed;
    align-items: center;
}
.note-form-inp,
.note-form-edit{
    margin-top: 1rem;
    border: 4px solid black;
    max-width: 350px;
    border-radius: 4px;
}
.note-form-text{
    display: flex;
    flex-direction: column;
}
.note-flex-form{
    display: flex;
    gap: 0.5rem;
}
.note-priority,
.note-priority-edit{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex: 1;
}
.note-form-close,
.note-form-close-edit{
    align-self: self-end;
}
.note-submit{
    border-radius: 2px;
    width: 100%;
}
.note-radio{
    display: flex;
    justify-content: space-between;
    align-items: center ;
    flex: 1;
}
input,
textarea{
    border-style: none;
}
button{
    border-style: none;
    background-color: black;
    color: white;
    font-size: 20px;
    height: 40px;
    width: 40px;
    font-weight: 700;
    border-radius: 50%;
}
.notes{
    display: grid;
    gap: 1rem;
}
#note-check{
    margin-left: 0.5rem;
}
.note-title{
    margin-left: 1rem;
}
.note-description{
    margin-left: 2rem
}
.note-due-date{
    margin-left: auto;
}
.note-edit{
    margin-left: 1rem;
    background-color: white;
}
.note-delete{
    margin-right: 0.25rem;
    margin-left: 1rem;
}
textarea{
    resize: none;
}
img{
    height: 40px;
}
input[type="image"]{
    height: 40px;
}
.note-form-close,
.note-form-close-edit{
    background-color: white;
}
.project-delete img{
    color: #e4dced;
}`, "",{"version":3,"sources":["webpack://./src/styles/style.css"],"names":[],"mappings":"AAAA;;IAEI,SAAS;IACT,UAAU;IACV,qCAAqC;AACzC;;AAEA;IACI,aAAa;IACb,aAAa;AACjB;AACA;IACI,eAAe;IACf,eAAe;AACnB;AACA;IACI,aAAa;IACb,sBAAsB;IACtB,QAAQ;IACR,cAAc;AAClB;AACA;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,eAAe;AACnB;AACA;IACI,eAAe;AACnB;AACA;;;IAGI,YAAY;IACZ,uBAAuB;IACvB,YAAY;IACZ,eAAe;IACf,YAAY;IACZ,YAAY;IACZ,gBAAgB;IAChB,kBAAkB;AACtB;AACA;IACI,YAAY;IACZ,YAAY;AAChB;AACA;IACI,yBAAyB;IACzB,4CAA4C;AAChD;;AAEA;IACI,gBAAgB;IAChB,kBAAkB;IAClB,uBAAuB;AAC3B;AACA;IACI,aAAa;IACb,sBAAsB;IACtB,QAAQ;IACR,gBAAgB;AACpB;AACA;IACI,OAAO;IACP,aAAa;IACb,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;AACA;IACI,oBAAoB;IACpB,eAAe;IACf,YAAY;IACZ,mBAAmB;AACvB;AACA;IACI,aAAa;IACb,aAAa;IACb,mCAAmC;IACnC,oBAAoB;IACpB,mBAAmB;AACvB;AACA;;IAEI,gBAAgB;IAChB,uBAAuB;IACvB,gBAAgB;IAChB,kBAAkB;AACtB;AACA;IACI,aAAa;IACb,sBAAsB;AAC1B;AACA;IACI,aAAa;IACb,WAAW;AACf;AACA;;IAEI,aAAa;IACb,sBAAsB;IACtB,8BAA8B;IAC9B,OAAO;AACX;AACA;;IAEI,oBAAoB;AACxB;AACA;IACI,kBAAkB;IAClB,WAAW;AACf;AACA;IACI,aAAa;IACb,8BAA8B;IAC9B,oBAAoB;IACpB,OAAO;AACX;AACA;;IAEI,kBAAkB;AACtB;AACA;IACI,kBAAkB;IAClB,uBAAuB;IACvB,YAAY;IACZ,eAAe;IACf,YAAY;IACZ,WAAW;IACX,gBAAgB;IAChB,kBAAkB;AACtB;AACA;IACI,aAAa;IACb,SAAS;AACb;AACA;IACI,mBAAmB;AACvB;AACA;IACI,iBAAiB;AACrB;AACA;IACI;AACJ;AACA;IACI,iBAAiB;AACrB;AACA;IACI,iBAAiB;IACjB,uBAAuB;AAC3B;AACA;IACI,qBAAqB;IACrB,iBAAiB;AACrB;AACA;IACI,YAAY;AAChB;AACA;IACI,YAAY;AAChB;AACA;IACI,YAAY;AAChB;AACA;;IAEI,uBAAuB;AAC3B;AACA;IACI,cAAc;AAClB","sourcesContent":["*{\n    \n    margin: 0;\n    padding: 0;\n    font-family: 'Roboto Mono', monospace;\n}\n\nbody{\n    height: 100vh;\n    display: flex;\n}\n.left{\n    margin: 0.25rem;\n    padding: 0.5rem;\n}\n.projects{\n    display: flex;\n    flex-direction: column;\n    gap: 5px;\n    color: #e4dced;\n}\n.project-div-btn{\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    padding: 0.5rem;\n}\n.project-div-btn h3{\n    font-size: 20px;\n}\n.p